.copyright__text {
  margin: 0;
  padding: 0;

  font-size: @text-alt-font-size;
  line-height: @text-alt-line-height;
  color: @alt-text-color;

  cursor: pointer;

  @media (min-width: @tablet-width) {
    font-size: @text-alt-tablet-font-size;
    line-height: @text-alt-tablet-line-height;
  }

  &:hover {
    color: @special-hover-blue-gray;
  }

  &:focus {
    color: @special-hover-blue-gray;
  }

  &:active {
    color: @special-active-blue-gray;
  }
}

.copyright__link {
  margin-left: auto;
  line-height: 0;
  color: @link-color;

  @media (min-width: @tablet-width) {
    margin-left: 23px;
  }

  &:hover {
    color: @special-hover-blue-gray;
  }

  &:focus {
    color: @special-hover-blue-gray;
  }

  &:active {
    color: @special-active-blue-gray;
  }
}
